.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

@media screen and (min-width: $query__large) {
    .alignleft,
    .wp-caption.alignleft {
        margin-left: -6.25rem;
    }
    .alignright, 
    .wp-caption.alignright {
        margin-right: -6.25rem;
    }
}

.aligncenter {
	clear: both;
	@include center-block;
}

.wp-caption.aligncenter,
.centered-image {
    max-width: 100vw;
    margin: 0 -1em 1.6em;
    
    @media screen and (min-width: $query__small) {
        margin: 0 -2em 1.6em;
    }
}